body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Sticker {
    padding-top: 50px;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 10px;
    background-color: #efbe43;
  }
.DMenu{
    position: absolute;
    right: 10px;
    top: 10px;
}
.Titulo{
    position: absolute;
    top: -5px;
    font-family: 'Raleway', sans-serif;
    font-size: 50px;
    font-style: italic;
  }
.Product{
    display: table;
    width: auto;
    margin: 0px;
    padding: 0px;
}
.Product:hover img{
    opacity: 0.3;

}
img {
    border-radius: 8px;
    height: auto; 
    width: auto; 
    max-width: 300px; 
    max-height: 300px;
    -webkit-transition: .5s ease;
    transition: .5s ease;
}
.middle {
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 45%;
    left: 17%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .Product:hover .middle {
    opacity: 1;
  }
  .text {
    background-color: rgba(65, 71, 65, 0.466);
    color: white;
    font-size: 16px;
    padding: 16px 32px;
  }
.data-container {
  padding-top: 50px;
  padding-right: 80px;
  padding-bottom: 4000px;
  padding-left: 80px;
  background-color: beige;
}
